.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todo-list-template {
    background: white;
    max-width:500px;
    width: 100%;
    box-shadow: 0 3px 10px rgba(0,0,0,0.16), 0 3px 50px rgba(0,0,0,0.23); /* 그림자 */ 
    margin: 0 auto; /* 페이지 중앙 정렬 */
    position: relative;
  }
  .header{
    background: #339af0;
    position: relative;
    color:#ffffff;
    display:flex;
    align-items: center;
  }
  .title{
    margin:0;
    padding: 1rem;
    font-size: 2rem;
    text-align: center;
    font-weight: normal;
    
    font-family:"Roboto", sans-serif;
    font-weight: 300;
    
  }
  .title a{
    color:#ffffff;
    text-decoration:none;
  }
  .auth-wrapper{
    margin-left:auto;
    font-size:1rem;
    line-height: 1rem;
  }
  .auth-wrapper div{
    display:flex;
    align-items: center;
  }

  .auth-wrapper .btn{
    color:#ffffff;
    display:inline-block;
    padding:.8rem;
    margin-right:.2rem;
  }
  .auth-wrapper .btn:hover{
    background:rgba(0,0,0,.3);
    border-radius:.3rem;
  }
  .btn i{
    font-size:2rem;
  }
  .form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #f1f3f5;
  }

  .sign-wrapper form{
    padding:20px;
    text-align: center;
    
  }
  .sign-wrapper div{
    position: relative;
  }
  .sign-wrapper label{
    position: absolute;
    color: #9e9e9e;
    top: 0.8rem;
    left: 0.75rem;
    font-size: 1rem;
    cursor: text;
    transition: .2s ease-out;
    vertical-align: middle;
  }
  .sign-wrapper input{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #9e9e9e;
    border-radius: 0;
    outline: none;
    height: 3rem;
    width: 100%;
    font-size: 1rem;
    margin: 0 0 15px 0;
    padding: 0;
    box-shadow: none;
    box-sizing: content-box;
  }
  .sign-wrapper input:focus{
    border-bottom: 1px solid #26a69a;
    box-shadow: 0 1px 0 0 #26a69a;
  }
  .sign-wrapper input:focus+label,
  .sign-wrapper .on+label{
    font-size:.8rem;
    -webkit-transform: matrix( 1, 0, 0, 1, -10, -20 );
            transform: matrix( 1, 0, 0, 1, -10, -20 );
  }
  .sign-wrapper button{
    width: 90%;
    display: inline-block;
    text-decoration: none;
    color: #fff;
    background-color: #26a69a;
    text-align: center;
    letter-spacing: .5px;
    padding:.8rem;
    border:none;
    border-radius:.3rem;
    cursor:pointer;
    font:500 1rem/1rem "Roboto", sans-serif;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    transition: all 0.3s;
  }
  .sign-wrapper button:hover{
    background-color: #2bbbad;
    box-shadow: 0 5px 11px 0 rgba(0,0,0,0.18), 0 4px 15px 0 rgba(0,0,0,0.15);
  }
  .sign-wrapper .footer{
    border-top: 1px solid #E9E9E9;
    padding: 20px;
    text-align: right;
  }
  .sign-wrapper .footer a{
    color: #039be5;
    text-decoration: none;
  }
  .sign-wrapper .footer a:hover{
    text-decoration: underline;
  }
  .todos-wrapper {
    min-height: 5rem;
  }

 

.form {
    display: flex;
  }
  
  .form input {
    font-size: .9rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #d0ebff;
    width:85%;
  }

  .form input.color1{
    color:#343a40;
  }
  .form input.color2{
    color:#f03e3e;
  }
  .form input.color3{
    color:#12b886;
  }
  .form input.color4{
    color:#228ae6;
  }

  .form input.bold-font{
    font-weight:bold;
  }
  
  .create-button {
    width:15%;
    box-sizing: border-box;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-left: 1rem;
    background: #1971c2;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
    border:none;
  }
  .create-button:hover {
    background: #a5d8ff;
  }
  .edit-button{
    width:15%;
    box-sizing: border-box;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-left: 1rem;
    background: #ff8787;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
    border:none;
  }
  .edit-button:hover {
    background: #ffc9c9;
  }

  .palette-with-btns{
    display:flex;
    align-items: center;
  }
  .sort-btn{
    margin-right:1.5rem;
    cursor: pointer;
    
  }
.palette {
    display: flex;
    justify-content: left;
    align-items: center;
  }
  .palette>span{
    height: 24px;
  }
  
  .color {
    opacity: 0.3;
    transition: all 0.2s;
    cursor: pointer;
  }

  .palette-color1{
    color:#343a40;
  }
  .palette-color2{
    color:#f03e3e;
  }
  .palette-color3{
    color:#12b886;
  }
  .palette-color4{
    color:#228ae6;
  }
  
  .color:hover {
    opacity: 1;
  }
  
  .color.active, .color:active {
    opacity: 1;
  }
  
  .color + .color {
    margin-left: .2rem;
  }

  .make-bold{
    display:flex;
    margin-left:.8rem;
    cursor:pointer;
  }
  .make-bold>img{
    width:100%;
  }
.todo-item {
    padding: 1rem .5rem;
    display: flex;
    align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    transition: all 0.15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .todo-item:hover {
    background: #e3fafc;
  }
  .edit-item{
    background: #e3fafc;
  }
  
  /* todo-item 에 마우스가 있을때만 .remove 보이기 */
  /* .todo-item:hover .remove {
    opacity: 1;
  } */
  
  /* todo-item 사이에 윗 테두리 */
  .todo-item + .todo-item {
    border-top: 1px solid #f1f3f5;
  }
  
  
  .remove {
    margin-right: 1rem;
    color: #e64980;
    font-size:1.8rem;
    line-height: 1.3rem;
    font-weight: 600;
    opacity: .5;
  }
  .remove.checked {
    opacity: 1;
  }
  .todo-text {
    flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }

  .todo-text.color1{
    color:#343a40;
  }
  .todo-text.color2{
    color:#f03e3e;
  }
  .todo-text.color3{
    color:#12b886;
  }
  .todo-text.color4{
    color:#228ae6;
  }
  
  .todo-text.checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  .todo-text.bold-font{
    font-weight:bold;
  }
  .check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin: 0 1rem;
    color: #3bc9db;
    font-weight: 800;
  }
  .edit-btn{
    margin-left:1rem;
    display:flex;
    align-items: center;
    justify-content: center;
  }
.realignment-item {
    padding: 1rem;
    display: flex;
    align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    transition: all 0.15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
  }
  
  .realignment-item:hover {
    background: #e3fafc;
  }

  /* todo-item 사이에 윗 테두리 */
  .realignment-item + .realignment-item {
    border-top: 1px solid #f1f3f5;
  }
    
  .realignment-item.r_checked{
    border:2px solid #84d335;
  }
  .realignment-text {
    flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }

  .realignment-text.color1{
    color:#343a40;
  }
  .realignment-text.color2{
    color:#f03e3e;
  }
  .realignment-text.color3{
    color:#12b886;
  }
  .realignment-text.color4{
    color:#228ae6;
  }

  .realignment-text.checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  .realignment-text.bold-font {
    font-weight: bold;
  }
  
  .realignment-btn{
      display: flex;
      margin-right:1rem;
      opacity:.1;
  }
  .realignment-btn.r_checked{
    color:#84d335;
  }
  .r_checked .realignment-btn{
      opacity:1;
  }

 /*base code*/
 .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  .animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
  }
  .animated.hinge {
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
  }
  /*the animation definition*/
  @-webkit-keyframes bounceIn {
    0%, 100%, 20%, 40%, 60%, 80% {
      transition-timing-function: cubic-bezier(0.215, .61, .355, 1)
    }
    0% {
      opacity: 0;
      -webkit-transform: scale3d(.3, .3, .3);
      transform: scale3d(.3, .3, .3)
    }
    20% {
      -webkit-transform: scale3d(1.1, 1.1, 1.1);
      transform: scale3d(1.1, 1.1, 1.1)
    }
    40% {
      -webkit-transform: scale3d(.9, .9, .9);
      transform: scale3d(.9, .9, .9)
    }
    60% {
      opacity: 1;
      -webkit-transform: scale3d(1.03, 1.03, 1.03);
      transform: scale3d(1.03, 1.03, 1.03)
    }
    80% {
      -webkit-transform: scale3d(.97, .97, .97);
      transform: scale3d(.97, .97, .97)
    }
    100% {
      opacity: 1;
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1)
    }
  }
  @keyframes bounceIn {
    0%, 100%, 20%, 40%, 60%, 80% {
      transition-timing-function: cubic-bezier(0.215, .61, .355, 1)
    }
    0% {
      opacity: 0;
      -webkit-transform: scale3d(.3, .3, .3);
      transform: scale3d(.3, .3, .3)
    }
    20% {
      -webkit-transform: scale3d(1.1, 1.1, 1.1);
      transform: scale3d(1.1, 1.1, 1.1)
    }
    40% {
      -webkit-transform: scale3d(.9, .9, .9);
      transform: scale3d(.9, .9, .9)
    }
    60% {
      opacity: 1;
      -webkit-transform: scale3d(1.03, 1.03, 1.03);
      transform: scale3d(1.03, 1.03, 1.03)
    }
    80% {
      -webkit-transform: scale3d(.97, .97, .97);
      transform: scale3d(.97, .97, .97)
    }
    100% {
      opacity: 1;
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1)
    }
  }
  .bounceIn {
    -webkit-animation-name: bounceIn;
    animation-name: bounceIn
  }
  @-webkit-keyframes bounceOutUp {
    20% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0)
    }
    40%,
    45% {
      opacity: 1;
      -webkit-transform: translate3d(0, 20px, 0);
      transform: translate3d(0, 20px, 0)
    }
    100% {
      opacity: 0;
      -webkit-transform: translate3d(0, -2000px, 0);
      transform: translate3d(0, -2000px, 0)
    }
  }
  @keyframes bounceOutUp {
    20% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0)
    }
    40%,
    45% {
      opacity: 1;
      -webkit-transform: translate3d(0, 20px, 0);
      transform: translate3d(0, 20px, 0)
    }
    100% {
      opacity: 0;
      -webkit-transform: translate3d(0, -2000px, 0);
      transform: translate3d(0, -2000px, 0)
    }
  }
  .bounceOutUp {
    -webkit-animation-name: bounceOutUp;
    animation-name: bounceOutUp
  }
  

